// LinoWebCam.tsx
/** @module LinoWebCam */

export const name: string = "LinoWebCam";

// @ts-ignore
import * as React from 'react';
// @ts-ignore
import PropTypes from 'prop-types';
import * as t from './types';
import { RegisterImportPool, getExReady } from "./Base";

// @ts-ignore
import Webcam from "react-webcam";

let ex; const exModulePromises = ex = {
    // @ts-ignore
    prButton: import(/* webpackChunkName: "prButton_LinoWebCam" */"primereact/button"),
}
RegisterImportPool(ex);

type LinoWebCamProps = {
    captureDone: (data: string) => void,
    height: number;
    width: number;
}

export const LinoWebCam: React.FC = (props: LinoWebCamProps) => {
    const webcamRef = React.useRef(null);

    const capture: React.MouseEventHandler<HTMLElement> = React.useCallback(
        (event) => {
            props.captureDone(webcamRef.current.getScreenshot());
        },
    [webcamRef]);

    const [deviceId, setDeviceId] = React.useState(null);
    const [devices, setDevices] = React.useState([]);

    const switchCamera: React.MouseEventHandler<HTMLElement> = React.useCallback(
        event => {
            let i = (devices.indexOf(deviceId) + 1) % devices.length;
            setDeviceId(devices[i]);
        },
    [devices, deviceId]);

    const videoConstraints: MediaTrackConstraints = {
        height: props.height, width: props.width}

    if (deviceId) videoConstraints.deviceId = deviceId
    else videoConstraints.facingMode = "environment";

    const localEx = getExReady(ex, ["prButton"], (mods) => {
        navigator.mediaDevices.enumerateDevices().then(devs => {
            setDevices(devs.filter(({kind}) => kind === "videoinput").map(d => d.deviceId))});
        return mods;
    });
    const buttonStyle = {background: "white", color: "black",
        borderRadius: "50%", width: "5ch", height: "5ch", margin: "5px"};

    return !devices.length ? null : <>
        <Webcam
            audio={false}
            forceScreenshotSourceSize={true}
            height={props.height}
            mirrored={true}
            onUserMedia={uM => {
                if (deviceId === null)
                    setDeviceId(uM.getVideoTracks()[0].getSettings().deviceId)}}
            ref={webcamRef}
            screenshotFormat="image/jpeg"
            videoConstraints={videoConstraints}
            width={props.width}
            />
        <div
            style={{position: "absolute", bottom: "40px", left: "50%",
                transform: "translate(-50%, 0)"}}>
            <localEx.prButton.Button
                icon="pi pi-camera"
                onClick={capture}
                style={buttonStyle}
            />
            {devices.length > 1 &&
                <localEx.prButton.Button
                    icon="pi pi-undo"
                    onClick={switchCamera}
                    style={buttonStyle}
                />
            }
        </div>
    </>
}


LinoWebCam.propTypes = {
    height: PropTypes.number.isRequired,
    width: PropTypes.number.isRequired
}
